function Booklist() {
	return (
		<main>
			<Navbar/>
			<section className="bookList">
				{/* mapping fuction allows for iteration of objects within the array */}
				{books.map((book) => {
					// return <List key={book.id} book={book} />;
					// using the spread operator allows the system to iterate over all the properties
					return <BookStructure key={book.id} {...book} />;
				})}
			</section>
			{/* First method is rendered here */}
			{/* <BookStructure bookOne={bookOne} /> */}
			{/* Second and third method are rendered here */}
			{/* <BookStructure1 title={bookOne.title} writer={bookOne.writer} /> */}
		</main>
	);
}

