import React from 'react'

const BookStructure = ({ img, title, author }) => {
	// While using spread operator then you dont to use the props.book instead you can use props alone
	// const { img, title, author } = props;
	const clickHandler = () => {
		alert("Hello world");
	};
	return (
		<article className="book">
			<img src={img} alt="humaaans png" />
			<h2>{title}</h2>
			<h4>{author}</h4>
			<button type="button" onClick={clickHandler}>
				Alert button
			</button>
		</article>
	);
};


export default BookStructure;

